/*      (a   .link */
* {
  box-sizing: border-box;
}

body {
  font-family: 'Mont', sans-serif;
  // font-weight: 800;
  letter-spacing: 0.04em;
  padding: 0;
  margin: 0;
  background-color: $white-color;
}

a {
  text-decoration: none;
  color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

/*      (ul   .list) */
ul {
  padding: 0;
  margin: 0;
}

.list {
  list-style: none;
}
/*      */
img {
  display: block;
  max-width: 100%;
  height: auto;
}

/*      address */
address {
  font-style: normal;
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}
