.program {
  padding: 33px 20px 60px;
  background-color: $dark-bg-color;
  color: $white-color;
}
.program-title {
  margin-bottom: 14px;
}

.program-descr-title {
  margin-bottom: 12px;
  font-weight: 800;
  font-size: 18px;
  line-height: 1.2;
  color: #dadada;
}

.program-descr {
  margin-bottom: 39px;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.7;
}

.program-box {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 32px;
}

.program-item {
  padding: 15px 20px;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  border: 1px solid #eeeeee80;
  border-radius: 10px;

  &:hover,
  &:focus {
    border: 2px solid $accent-light-color;
  }
}

.program-item-title {
  font-size: 24px;
  font-family: Druk Cyr;
  font-weight: 700;
  letter-spacing: 0.48px;
  text-transform: uppercase;
}
.program-item-train-title {
  margin-top: 24px;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.3;
  letter-spacing: -0.02em;
  text-transform: none;
}

.program-item-train-descr {
  margin-top: 6px;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.7;
  color: #dadada;
}
