}

body {
  letter-spacing: .04em;
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: Mont, sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p, ul {
